@charset "UTF-8";
.portal-content-box {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  color: #3e63dd;
  cursor: pointer;
  flex: 0 0 calc(33.33% - 1.5em);
  max-width: 350px;
  min-width: 280px;
  padding: 1em;
  position: relative;
  text-decoration: none;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
}
.portal-content-box:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  transform: translateY(-8px) scale(1.02);
}
.portal-content-box:active {
  transform: scale(0.98);
}
.portal-content-box .portal-content-box-content {
  color: #555;
  font-size: 0.95em;
  list-style: none;
  margin-top: 0.5em;
  padding: 8px 8px 8px 0;
}
.portal-content-box .portal-content-box-content li {
  border-radius: 4px;
  list-style: none;
  margin-top: 0.4em;
  padding: 8px;
  transition: background-color 0.2s ease-out, color 0.2s ease-out;
}
.portal-content-box .portal-content-box-content li:hover {
  background-color: rgba(62, 99, 221, 0.1);
  color: #3e63dd;
}
.portal-content-box h3.portal-content-box-headline {
  border-bottom: 2px solid #3e63dd;
  color: #3e63dd;
  font-size: 1.18em;
  font-weight: 500;
  letter-spacing: 1.5px;
  margin: 0 auto;
  max-width: 90%;
  padding-bottom: 0.4em;
  text-align: center;
}
.portal-content-box:after {
  align-items: center;
  background-color: rgba(62, 99, 221, 0.05);
  border-radius: 5px;
  bottom: 10px;
  color: #3e63dd;
  content: "â†’";
  display: flex;
  font-size: 1.2em;
  justify-content: center;
  opacity: 0.8;
  padding: 5px 8px;
  position: absolute;
  right: 10px;
  transition: background-color 0.3s ease;
}
.portal-content-box:after:hover:after {
  background-color: rgba(62, 99, 221, 0.25);
}
.portal-content-box .neo-mouse:hover {
  background-color: rgba(62, 99, 221, 0.05);
}
.portal-content-box .neo-mouse:hover .portal-content-box-headline {
  border-bottom-color: #1a47b8;
}
.portal-content-box .neo-no-mouse:after {
  bottom: 20px;
  color: #3e63dd;
  content: "\f08e";
  font-weight: 600;
  position: absolute;
  right: 0.5em;
}